import React, {useState} from 'react';
import s from './footer.module.css'
import styleContainer from "../common/styles/Container.module.css";
import IconOfSocial from "./IconOfSocial";
import Title from "../common/components/title/Title";
import mailIcon from './../img/icons/mail.png'


const Footer = () => {

    const [active, setActive] = useState(false)

    const instagram = 'https://upload.wikimedia.org/wikipedia/commons/thumb/a/a5/Instagram_icon.png/600px-Instagram_icon.png';
    const linkedIn = 'https://image.flaticon.com/icons/png/512/174/174857.png'
    //const twitter = 'https://www.google.com.ua/url?sa=i&url=https%3A%2F%2Fwww.pngegg.com%2Fru%2Fpng-oudlr&psig=AOvVaw3GMjCF03UAXQBELsT1PPbb&ust=1637317838668000&source=images&cd=vfe&ved=0CAsQjRxqFwoTCOCu37naofQCFQAAAAAdAAAAABAc'
    const facebook = 'https://upload.wikimedia.org/wikipedia/commons/thumb/0/05/Facebook_Logo_%282019%29.png/1024px-Facebook_Logo_%282019%29.png'

    const linkInstagram = 'https://instagram.com/dmitriy_shpak11?r=nametag'
    const linkLinkedIn = 'https://www.linkedin.com/in/dmitriy-shpak-13b15321a/'
    const linkFacebook = 'https://www.facebook.com/profile.php?id=100005371338520'
   // const linkMail = 'https://instagram.com/dmitriy_shpak11?r=nametag'

    const showMail = () => {
        setActive(!active)
    }

    return (
        <div className={s.footerBlock}>
            <div className={`${styleContainer.container} ${s.footerContainer}`}>
                <div className={s.footerInner}>
                    <Title text={'Dmitriy Shpak'}/>
                    <div className={s.socialBlock}>
                        <IconOfSocial icon={instagram} href={linkInstagram}/>
                        <IconOfSocial icon={linkedIn} href={linkLinkedIn}/>
                        <IconOfSocial icon={facebook} href={linkFacebook}/>
                        <IconOfSocial icon={mailIcon} showMail={showMail} active={active}/>
                    </div>
                    <h2 className={s.secondTitle}>
                        <span className={s.specialSymbol}>2021</span> All rules are secured
                    </h2>
                </div>
            </div>

        </div>
    );
};

export default Footer;