import React from 'react';
import s from './iconSocial.module.css'

const IconOfSocial = (props) => {

    return (
        <>
            <div className={s.iconContainer}>
                <a href={props.href} target="_blank" onClick={props.showMail}>
                    <img src={props.icon} className={s.icon}/>
                </a>
            </div>
            {props.active && <div className={s.mail}>
                dmitriykoms97@gmail.com
            </div>}
        </>
    );
};

export default IconOfSocial;